/**
 * @author rubyisapm
 * 
 *
 * 
 * 1. key(key:; val:)
 * 2. key valkey val
 * 3. ''''''
 * 4. ''''''
 *
 * 
 * : auditStatus
 * : portraitType
 *
 * 
 * constConfigauditStatusportraitTypeconstConfig
 *
 * :
 * 1. constConfig.getData
 * 2. 
 * constConfig.portraitType().then(function(){this.portraitTypeList=constConfig.getData('portraitType')})
 *
 * constConfig:
 * !!: 
 * 1. getData 
 * 2. getVal auditStatuskey1val
 * 3. getKey keyauditStatusval''key
 */
import utility from 'ct-utility';
import Interface from 'common/interface';
var constConfig = {
    //''''''
    os: [{
        key: 0,
        val: ''
    }, {
        key: 1,
        val: 'android'
    }, {
        key: 2,
        val: 'ios'
    }],
    switchStatus: [{
        key: '',
        val: ''
    }, {
        key: true,
        val: ''
    }, {
        key: false,
        val: ''
    }],
    appType: [{
        key: 0,
        val: ''
    }, {
        key: 1,
        val: ''
    }, {
        key: 2,
        val: ''
    }, {
        key: 3,
        val: ''
    }],
    /**
     * 
     * promisepromise
     *
     * 
     * portraitType.data
     * portraitType.data
     *
     * 
     * portraitTypeconstConfig.portraitType().then(function(){this.portraitTypeList=constConfig.getData('portraitType')})
     * this.portraitTypeListvueportraitType.
     */
    portraitType(){
        const that = constConfig;
        let p;

        //length>0
        if (Array.isArray(that.portraitType.data) && that.portraitType.data.length > 0) {
            p = Promise.resolve(that.portraitType.data);
        } else {
            //
            p = Promise.resolve($.ajax({
                url: Interface.common.getPortraitTypeList,
                cache: false
            })).then(res=> {
                //key val
                res = utility.objTransfer.lowerKey(res);

                if (res.statusCode === 0) {
                    that.portraitType.data = [{
                        key: 0,
                        val: ''
                    }];
                    res.data.map(item=> {
                        that.portraitType.data.push({
                            key: item.key,
                            val: item.val
                        });
                    });
                } else {
                    //warning!
                    that.portraitType.data = [];
                }
            }).catch(()=> {
                //warning!
                that.portraitType.data = [];
            });
        }
        //promiseportraitType
        return p;
    },
    /**
     * 
     * @param col String 'auditStatus'
     * @param hasDef Boolean ''/''
     * @param def String hasDeftrue'''',def''
     */
    getData(col, hasDef = true, def = ''){
        let data;
        const that = constConfig;

        if (Array.isArray(that[col])) {
            data = JSON.parse(JSON.stringify(that[col]));
        } else if (Array.isArray(that[col].data) && that[col].data.length > 0) {
            data = JSON.parse(JSON.stringify(that[col].data));
        }

        if (!hasDef) {
            data = data.slice(1);
        } else if (def !== '') {
            data[0].val = def;
        }
        return data;
    },
    /**
     * keyval
     * @param col String 'auditStatus'
     * @param key [js] valkey
     * @param def String '''',def''
     */
    getVal(col, key, def = ''){
        const data = constConfig.getData(col);
        const matchedItem = data.filter((item)=> {
            return item.key === key;
        });

        if (matchedItem[0].val === '' && (def === '' || def === '')) {
            return def;
        }
        return matchedItem[0].val;
    },
    /**
     * valkey
     * @param col String 'auditStatus'
     * @param val String keyval
     */
    getKey(col, val){
        if (val === '' || val === '') {
            val = '';
        }
        const data = constConfig.getData(col);
        const matchedItem = data.filter((item)=> {
            return item.val === val;
        });

        return matchedItem[0].key;
    }
};

export default constConfig;



